body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    background-color: #000;
    height: 100vh;
}

.addNew {
    border-style: dotted;
    border-radius: 15px;
    padding: 20px;
    font-size: 1.7rem;
    font-weight: bold;
    color: #9c92869f;
}

.heading {
    font-size: 2.5rem;
    font-weight: bold;
    color: #fff;
    text-align: center;
    padding: 1em 0;
    color: #efc99d;
}

:root{
    --input__font: 'Montserrat', sans-serif;
}

.Input_input__29Zev{
    background-color: #1e1f23;
    width: 70%;
    border-radius: 15px;
    padding: 20px;
    display: grid;
    grid-gap: 20px;
    gap: 20px;
    place-items: left;
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

input{
    padding: 15px;
    background-color: black;
    border: none;
    border-radius: 15px;
    color: #efc99d;
    font-family: 'Montserrat', sans-serif;
    font-family: var(--input__font);
}


label{
    color: #efc99d;
    font-size: 1.2rem;
    font-weight: 600;
    font-family: 'Montserrat', sans-serif;
    font-family: var(--input__font);
}

.Input_input__29Zev button{
    border: none;
    border-radius: 15px;
    padding: 10px 20px;
    color: #1e1f23;
    background-color: #efc99d;
    font-size: 1.2rem;
    font-weight: bold;
    font-family: 'Montserrat', sans-serif;
    font-family: var(--input__font);
}

.Input_input__field__1rNJG{
    width: 100%;
    display: grid;
    grid-gap: 10px;
    gap: 10px;
}

.Input_input__backdrop__2lN4S{
    position: fixed;
    height: 100vh;
    width: 100% !important;
    -webkit-backdrop-filter: blur(2px);
            backdrop-filter: blur(2px);
    left: 0;
}
.wrapper{
    width: 80%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 20px;
    gap: 20px;
}
:root {
    --counter__font: "Montserrat", sans-serif;
}

.counter {
    color: white;
    font-family: "Montserrat", sans-serif;
    font-family: var(--counter__font);
}

.counter__container {
    display: grid;
    grid-template-areas:
        "title title title count count"
        "desc desc desc desc desc"
        "buttons buttons buttons buttons buttons";
    background-color: #1e1f23;
    border-radius: 15px;
    padding: 20px;
    grid-gap: 20px;
    gap: 20px;
}

.counter__title {
    grid-area: title;
    font-size: 1.5rem;
    font-weight: bold;
    color: #efc99d;
}

.counter__count {
    grid-area: count;
    font-size: 1.5rem;
    font-weight: bold;
    text-align: center;
    color: #1e1f23;
    background-color: #efc99d;
    border-radius: 15px;
}

.counter__description {
    grid-area: desc;
    font-size: 1rem;
    color: #9c9286;
}

.buttons{
    grid-area: buttons;
    display: flex;
    justify-content: space-around;
}

.counter__inc {
    background-color: #efc99d;
    border-radius: 15px;
    padding: 10px;
    cursor: pointer;
    border: none;
    /* height: 50px; */
    width: 30%;
    font-weight: 900;
    font-size: 1.5rem;
    line-height: 30px;
}

.counter__dec {
    border: 1px solid #efc99d;
    background-color: transparent;
    color: #efc99d;
    border-radius: 15px;
    padding: 10px;
    cursor: pointer;
    /* height: 50px; */
    width: 30%;
    font-weight: 900;
    font-size: 1.5rem;
    line-height: 30px;
}

